import React from "react";
import LayerA from './LayerA';
import '../styles/wd.css';
import Tf from './tf';


class Wd extends React.Component {

    state = {
        query : '찬란한 붉은빛 엠블렘[힘]',
        json : null,
        isFetching : false
    };

    componentDidMount () {
        this.fetchDefault();
    }

    fetchDefault = () => {
        this.setState({...this.state, isFetching: true});
        let origin = window.origin;
        //fetch(`https://osh1995.info/api/${this.state.query}`)
        fetch(`${origin}/api/${this.state.query}`)
            .then(response => response.json())
            .then(result => {
                //console.log(result);
                this.setState({
                    json: result,
                    query : this.state.query,
                    isFetching: false
                })
            })
            .catch(e => console.log(e));
    };

    siptListener = (e) => {
        let query = document.querySelector("#autoComplete").value;
        if(e.key === 'Enter' && query.length !== 0) this.sendQuery(query);
    };
    sendQuery = (query) => {
        this.setState({...this.state, isFetching: true});
        fetch(`/api/${query}`)
            .then(response => response.json())
            .then(result => {
                console.log(result);
                this.setState({
                    json: result,
                    query : query,
                    isFetching: false
                })
            })
            .catch(e => console.log(e));
    };
    onEnter = (query) => {
        this.sendQuery(query);
    };

    render() {
        let renderData = this.state.json || {};
        let renderArray = Object.keys(renderData);
        let query = this.state.query;
        let a = renderData[0] || {};
        let b = a[query] || [];
        let c = b.rows || [];
        let d = c[0] || {};
        console.log(d.itemId);
        let img = d.itemId ||'43378c2c961612d37ea73a16aeecaa9e';
        return (
            <div className={"wd"}>
                <div className={"wd__elem1"}></div>
                <div className={"wd__elem2 --horizontal"}>
                    <div className={"wd__elem2__elem1"}>
                        <Tf onEnter={this.onEnter}/>

                        <div className={"summary-data"}></div>
                        <div className={"recent-search"}></div>
                    </div>
                    <div className={"wd__elem2__elem2 --vertical"}>
                        <div className={"wd__elem2__elem2__elem1"}>
                            <div className={"wd__elem2__elem2__elem1__query-name"}>
                                <img src={`https://img-api.neople.co.kr/df/items/${img}`}></img>
                                <div>{ query }</div>
                            </div>
                        </div>
                        {
                                renderArray.map((idx) => {
                                    return (
                                        <LayerA
                                            key = { idx }
                                            query = { query }
                                            data = { renderData[idx] } />

                                    )
                                })
                        }
                    </div>
                    <div className={"layer-e"}>
                        TEST

                    </div>


                </div>
                <div className={"footer"}></div>

                <div>

                </div>
            </div>
        )
    }

}


export default Wd;